.tldr {
    margin: var(--size-margin-30) 0;
    >.fade-in {
        >.tldr-title {
            font-size: var(--size-font-size-preview-title);
            margin-bottom: var(--size-margin-10);
        }
        .code-element {
            font-family: 'Hack';
            background-color: var(--color-very-light-yellow);
        }
    }
}